import { applyMiddleware, combineReducers, createStore, compose } from 'redux'
import { profileReducer } from './profileReducerTypescript'
import { dialogsReducer } from './dialogsReducerTypescript'
import { friendsReducer } from './friendsReducer'
import { usersReducer } from './usersReducerTypescript'
import { authReducer } from './authReducerTypescript'
import { appReducer } from './appReducerTypescript'
import { headerReducer } from './myFirstTypescriptReducer'
import thunkMiddleware from 'redux-thunk'


let reducers = combineReducers({
    profileReducer,
    dialogsReducer,
    friendsReducer,
    usersReducer,
    authReducer,
    appReducer,
    headerReducer
})
const composeEnhancers = window.__REDUX_DEVTOOLS_EXTENSION_COMPOSE__ || compose;
const store = createStore(reducers, composeEnhancers(applyMiddleware(thunkMiddleware)));

// let store = createStore(reducers, applyMiddleware(thunkMiddleware));

window.store = store

export default store