const POST_NEW_TEXT = 'POST_NEW_TEXT'

type newDialogType = {
    id: number
    name: string
}
type newPostItemType = {
    id: number
    message: string
}

type initialStateType = {
    dialogsData: Array<newDialogType>
    messagesData: Array<newPostItemType>
}

type actionType = {
    type : typeof POST_NEW_TEXT
    postText: string
}

let initialState : initialStateType  = {
    dialogsData: [],
    messagesData: [],
}

export const dialogsReducer = (state = initialState, action : actionType) : initialStateType => {

    switch (action.type) {
        case POST_NEW_TEXT:
            let newDialog : newDialogType = {
                id: 4,
                name: 'Tema'
            }
            let newPostItem : newPostItemType = {
                id: 4,
                message: action.postText
            }
            return {
                ...state,
                dialogsData: [...state.dialogsData, newDialog],
                messagesData: [...state.messagesData, newPostItem],
            }
        default:
            return state
    }
}



export const postNewTextActionCreator = (postText : string) => {
    return {
        type: POST_NEW_TEXT,
        postText: postText
    }
}
