import { userType } from './../types/types';
import { follow, unFollow, getUsers } from '../API/api'

const FOLLOW = 'FOLLOW'
const UNFOLLOW = 'UNFOLLOW'
const SET_USERS = 'SET_USERS'
const PAGE_SIZE = 'PAGE_SIZE'
const TOGGLE_PRELOADER = 'TOGGLE_PRELOADER'
const TOGGLE_FOLLOWING_PROGRESS = 'TOGGLE_FOLLOWING_PROGRESS'
const PAGE_COUNT = 'PAGE_COUNT'
const TOTAL_COUNT = 'TOTAL_COUNT'




let initialState = {
    users: [] as Array<userType>,
    pageSize: 100,
    pageCount: 1,
    totalCount: 0,
    isFetching: false,
    followingProgress: [] as Array<number>,
}

type initialStateType = typeof initialState

export const usersReducer = (state : initialStateType = initialState, action : any) => {
    switch (action.type) {
        case FOLLOW:
            return {
                ...state,
                users: state.users.map(user => {
                    if (user.id === action.userId) {
                        return { ...user, followed: true }
                    }
                    return user
                })
            }
        case UNFOLLOW:
            return {
                ...state,
                users: state.users.map(user => {
                    if (user.id === action.userId) {
                        return { ...user, followed: false }
                    }
                    return user
                })
            }
        case SET_USERS:
            return {
                ...state,
                users: [...action.users]
            }
        case PAGE_SIZE:
            return {
                ...state,
                pageSize: action.pageSize
            }
        case PAGE_COUNT:
            return {
                ...state,
                pageCount: action.pageCount
            }
        case TOTAL_COUNT:
            return {
                ...state,
                totalCount: action.totalCount
            }
        case TOGGLE_PRELOADER:
            return {
                ...state,
                isFetching: action.isFetching
            }
        case TOGGLE_FOLLOWING_PROGRESS:
            return {
                ...state,
                followingProgress: action.followingProgress ? [...state.followingProgress, action.userId]
                    : state.followingProgress.filter(id => id !== action.userId)
            }
        default:
            return state
    }
}
type followACType = {
    type: typeof FOLLOW
    userId: number
}
export const followAC = (userId : number) : followACType => ({ type: FOLLOW, userId })

type unfollowACType = {
    type: typeof UNFOLLOW
    userId: number
}
export const unfollowAC = (userId : number) : unfollowACType => ({ type: UNFOLLOW, userId })

type setUsersACType = {
    type: typeof SET_USERS
    users: Array<userType>
}
export const setUsersAC = (users : Array<userType>) : setUsersACType => ({ type: SET_USERS, users })

type pageSizeACType = {
    type: typeof PAGE_SIZE
    pageSize: number
}
export const pageSizeAC = (pageSize : number) : pageSizeACType => ({ type: PAGE_SIZE, pageSize })

type pageCountACType = {
    type: typeof PAGE_COUNT
    pageCount: number
}
export const pageCountAC = (pageCount: number) : pageCountACType => ({ type: PAGE_COUNT, pageCount })

type totalCountARType = {
    type: typeof TOTAL_COUNT
    totalCount: number
}
export const totalCountAR = (totalCount : number) : totalCountARType => ({ type: TOTAL_COUNT, totalCount })

type togglePreloaderACType = {
    type: typeof TOGGLE_PRELOADER
    isFetching: boolean
}
export const togglePreloaderAC = (isFetching : boolean) : togglePreloaderACType => ({ type: TOGGLE_PRELOADER, isFetching })

type toggleFollowingProgressACType = {
    type: typeof TOGGLE_FOLLOWING_PROGRESS
    followingProgress: boolean
    userId: number
}
export const toggleFollowingProgressAC = (followingProgress : boolean, userId: number) : toggleFollowingProgressACType => 
({ type: TOGGLE_FOLLOWING_PROGRESS, followingProgress, userId })

export const getUsersThunkCreator = (pageSize: number, pageCount : number) =>
    async (dispatch : any) => {
        dispatch(pageCountAC(pageCount))
        const res = await getUsers(pageSize, pageCount)
        dispatch(setUsersAC(res.items))
        dispatch(totalCountAR(res.totalCount))
    }


export const removeUsersThunkCreator = () => {
    return (dispatch : any) => {
        dispatch(setUsersAC([]))
    }
}

export const followThunkCreator = (userId : number) =>

    async (dispatch : any) => {
        dispatch(toggleFollowingProgressAC(true, userId))
        const res = await follow(userId)
        if (res.data.resultCode === 0) {
            dispatch(followAC(userId))
        }
        dispatch(toggleFollowingProgressAC(false, userId))
    }


export const unFollowThunkCreator = (userId : number) =>
    async (dispatch : any) => {
        dispatch(toggleFollowingProgressAC(true, userId))
        const res = await unFollow(userId)
        if (res.data.resultCode === 0) {
            dispatch(unfollowAC(userId))
        }
        dispatch(toggleFollowingProgressAC(false, userId))
    }






