import React from 'react'
import { connect } from 'react-redux'
import Header from './Header'
import { logOut } from '../../redux/authReducer'
import { withRouter } from "react-router-dom";
import { compose } from 'redux';
import axios from 'axios'
import { getUserProfilePhotoThunk } from '../../redux/headerRecuder';


class HeaderContainer extends React.Component {

    constructor(props) {
        super(props)
        this.props = props
    }

    componentDidMount() { 
        this.props.getUserProfilePhotoThunk(this.props.userId)
    }

    render() {
        return <Header {...this.props} />
    }
}

const mapStateToProps = (state) => ({
    isLoggedIn: state.authReducer.isLoggedIn,
    login: state.authReducer.login,
    isFetching: state.authReducer.isFetching,
    userId: state.authReducer.userId,
    userProfilePhoto: state.headerReducer.userProfilePhoto
})

export default compose(
    withRouter,
    connect(mapStateToProps, { logOut, getUserProfilePhotoThunk })
)(HeaderContainer) 