import { loginMe, logInProfile, getCaptcha, logOutMe } from '../API/api'

const SET_USER_DATA = 'SET_USER_DATA'
const LOGGED_IN_TOGGLE = 'LOGGED_IN_TOGGLE'
const LOGGED_IN_FETCHING = 'LOGGED_IN_FETCHING'
const CAPTCHA = 'CAPTCHA'

type initialStateType = {
    userId: number | null
    email: string | null
    login: string | null
    isLoggedIn: boolean
    isFetching: boolean
    confirmation: boolean
    captchaImg: string
}


let initialState : initialStateType = {
    userId: null,
    email: null,
    login: null,
    isLoggedIn: false,
    isFetching: false,
    confirmation: false,
    captchaImg: '',
}

export const authReducer = (state = initialState, action : any) : initialStateType => {
    switch (action.type) {
        case SET_USER_DATA:
            return {
                ...state,
                ...action.data,
            }
        case LOGGED_IN_TOGGLE:
            return {
                ...state,
                isLoggedIn: action.isLoggedIn
            }
        case LOGGED_IN_FETCHING:
            return {
                ...state,
                isFetching: action.isFetching,
                confirmation: action.confirmation
            }
        case CAPTCHA:
            return {
                ...state,
                captchaImg: action.captchaImg
            }
        default:
            return state
    }
}

type setUserDataACDataType = {
    userId: null | number
    email: null | string
    login: null | string
    isLoggedIn: null | boolean
}
type setUserDataACType = {
    type : typeof SET_USER_DATA
    data : setUserDataACDataType
}

export const setUserDataAC = 
(userId : number | null, email : string | null, login : string | null, isLoggedIn : boolean) 
: setUserDataACType => {
    return {
        type: SET_USER_DATA,
        data: { userId, email, login, isLoggedIn: isLoggedIn },
    }
}


type LoggedInTogglegACType = {
    type : typeof LOGGED_IN_TOGGLE
    isLoggedIn : boolean
}
export const LoggedInTogglegAC = (isLoggedIn : boolean) : LoggedInTogglegACType => {
    return {
        type: LOGGED_IN_TOGGLE,
        isLoggedIn: isLoggedIn
    }
}

type LoggedInFetchingACType = {
    type : typeof LOGGED_IN_FETCHING
    isFetching: boolean
    confirmation: boolean
}

export const LoggedInFetchingAC = (isFetching : boolean, confirmation : boolean) : LoggedInFetchingACType => {
    return {
        type: LOGGED_IN_FETCHING,
        isFetching: isFetching,
        confirmation: confirmation
    }
}

type captchaType = {
    type: typeof CAPTCHA
    captchaImg: string
}
export const captcha = (captchaImg : string) : captchaType => ({
    type: CAPTCHA,
    captchaImg: captchaImg
})

export const logInUserThunkCreator = () => async (dispatch : any) => {
    dispatch(LoggedInFetchingAC(true, false))
    const res = await logInProfile()
    if (res.data.resultCode === 0) {
        let { id, email, login } = res.data.data
        dispatch(setUserDataAC(id, email, login, true))
    }
    dispatch(LoggedInFetchingAC(false, true))
}

export const login = (email : string, password : string, rememberMe : boolean, captcha : any) =>
    async (dispatch : any) => {
        const res = await loginMe(email, password, rememberMe, captcha)
        if (res.data.resultCode === 0) {
            dispatch(logInUserThunkCreator())
        }
        if (res.data.resultCode === 10) {
            dispatch(getCaptchaThunk())
        }
    }


export const logOut = () =>
    async (dispatch : any) => {
        const res = await logOutMe()
        if (res.data.resultCode === 0) {
            dispatch(setUserDataAC(null, null, null, false))
        }
    }


export const getCaptchaThunk = () =>
    async (dispatch : any) => {
        const res = await getCaptcha()
        dispatch(captcha(res.data.url))
    }


