import React from 'react'
import { updateUserProfileSettings, getUserProfileThunkCreator, removeUserProfileThunkCreator } from '../../redux/profileReducer'
import { connect } from "react-redux";
import Settings from './Settings';
import { compose } from 'redux';
import { withRouter } from "react-router-dom";
import { LoggedInRedirect } from '../../hoc/LoggedInRedirect';


class SettingsContainer extends React.Component {

    componentDidMount() {
        this.props.getUserProfileThunkCreator(this.props.userId)
    }
    componentWillUnmount() {
        this.props.removeUserProfileThunkCreator()
    }
    render() {
        return <>
            {
                !this.props.profile
                    ? null
                    : <Settings
                        sendProfileSettings={this.props.updateUserProfileSettings}
                        profile={this.props.profile}
                    />
            }

        </>
    }
}

let mapStateToProps = (state) => ({
    profile: state.profileReducer.profile,
    userId: state.authReducer.userId
})

export default compose (
    withRouter, 
    connect(mapStateToProps, { 
        updateUserProfileSettings, 
        getUserProfileThunkCreator, 
        removeUserProfileThunkCreator 
    }),
    LoggedInRedirect
) 
(SettingsContainer) 