import {
    getUserProfile,
} from '../API/api'

const SET_USER_PROFILE_PHOTO : string = 'SET_USER_PROFILE_PHOTO';

export type initialStateType = {
    userProfilePhoto: string
}

type actionType = {
    type: string,
    userProfilePhoto: string,
}

let initialState : initialStateType = {
    userProfilePhoto: '',
}

export const headerReducer = (state = initialState, action : actionType) : initialStateType => {
    switch(action.type) {
        case SET_USER_PROFILE_PHOTO: {
            return { ...state, userProfilePhoto: action.userProfilePhoto }
        }
        default:
            return state
    }
}

export const setUserProfilePhoto = (userProfilePhoto : string) : actionType => ({type: SET_USER_PROFILE_PHOTO, userProfilePhoto})

export const getUserProfilePhotoThunk = (UserId : string) => 
    async (dispatch : any) => {
        const res = await getUserProfile(UserId) 
        dispatch(setUserProfilePhoto(res.data.photos.small))
    }
