import { logInUserThunkCreator } from "./authReducer"

const INITIALIZED_SUCCSESS = 'INITIALIZED_SUCCSESS'

type initialStateType = {
    initialized : boolean
}

type actionType = {
    type: typeof INITIALIZED_SUCCSESS
}

let initialState : initialStateType = {
    initialized: false,
}

export const appReducer = (state = initialState, action : actionType) : initialStateType => {
    switch (action.type) {
        case INITIALIZED_SUCCSESS:
            return {
                ...state,
                initialized: true
            }
        default:
            return state
    }
}

export const initializeSuccsess = () : actionType => ({ type: INITIALIZED_SUCCSESS })

export const initializeApp = () => (dispatch : Function) => {
    let logInUserPromise = dispatch(logInUserThunkCreator())
    Promise.all([logInUserPromise]).then(() => {
        dispatch(initializeSuccsess())
    })
}