import React from 'react'
import classes from './Header.module.css'
import { NavLink } from 'react-router-dom'
import defaultAvatar from '../../assets/defaultAvatar.png'


const Header = (props) => {
    console.log(typeof props.userId)
    const logout = () => {
        props.logOut()
    }

    const showUserSettingsContainer = (event) => {
        let userSettings = document.getElementById('profileSettings')
        userSettings.style.display = 'block';
    }
    const hideUserSettingsContainer = (event) => {
        let userSettings = document.getElementById('profileSettings')
        userSettings.style.display = 'none';
    }

    return (
        <header className={classes.header}>
            <div className={classes.login}>
                {props.isLoggedIn ?
                    <div
                        onMouseOver={showUserSettingsContainer}
                        onMouseOut={hideUserSettingsContainer}>

                        <div
                            className={classes.userInfo}>
                            <div className={classes.userLogin}>
                                <div><span>{props.login}</span></div>
                            </div>
                            <NavLink to='/Profile'>
                                <div className={classes.userAvatar}>
                                    <img src={props.userProfilePhoto || defaultAvatar} alt="User avatar" />
                                </div>
                            </NavLink>
                        </div>
                        <div id='profileSettings' className={classes.profileSettings}>
                            <div>
                                <button onClick={logout}>Log out</button>
                            </div>
                            <div>
                                <NavLink to='/Settings'>
                                    <button onClick={hideUserSettingsContainer} >
                                        Profile settings
                                </button>
                                </NavLink>
                            </div>
                        </div>
                    </div>
                    : <NavLink to='/Login'> <button>Login</button></NavLink>}

            </div>
        </header>
    )
}
export default Header 