import React from 'react'
import { Formik, Form } from 'formik'
import FormikControl from '../Common/FormControls/FormikControl'
import * as Yup from 'yup'
import { login } from '../../redux/authReducer'
import { connect } from 'react-redux'
import { Redirect } from 'react-router-dom'
import classes from './LoginForm.module.css'

const LoginForm = (props) => {

    const { login, isLoggedIn, captchaImg } = props


    const initialValues = {
        email: '',
        password: '',
        rememberMe: true,
        captcha: '',
    }

    const validationSchema = Yup.object({
        email: Yup.string().email('Invalid email format').required('Enter email'),
        password: Yup.string().required('Enter password'),
    })

    const onSubmit = values => {
        login(values.email, values.password, values.rememberMe, values.captcha)
    }

    if (isLoggedIn) { return <Redirect to={'/Profile'} /> } // Проверяем, если мы залогинены, то редиректим на профиль

    return (
        <Formik initialValues={initialValues} validationSchema={validationSchema} onSubmit={onSubmit}>
            {
                formik => {
                    return <Form>
                        <h1>Login</h1>
                        <FormikControl
                            email={formik.values.email}
                            touchedEmail={formik.touched.email}
                            control='input'
                            type='email'
                            label='Email'
                            name='email' />
                        <FormikControl
                            password={formik.values.password}
                            touchedPassword={formik.touched.password}
                            control='input'
                            type='password'
                            label='Password'
                            name='password'
                        />
                        <p></p>
                        <input type='submit' disabled={!formik.isValid} value={'Login'} />
                        {!captchaImg ?
                            null
                            : <div className={classes.captchaImg}>
                                <p></p>
                                <img src={captchaImg} alt="captcha" />
                                <FormikControl
                                    control='input'
                                    label='Enter your captcha'
                                    name='captcha'
                                />
                            </div>
                        }
                    </Form>
                }
            }
        </Formik>
    )
}
export let mapStateToProps = (state) => {
    return {
        isLoggedIn: state.authReducer.isLoggedIn,
        captchaImg: state.authReducer.captchaImg
    }
}

export default connect(mapStateToProps, { login })(LoginForm)