import {
    getUserProfile,
    getUserStatus,
    sendUserStatus,
    sendUserPhoto,
    sendUserProfileSettings
} from '../API/api'

import {
    postsType,
    contactsType,
    photosType,
    profileType
} from '../types/types'

const SET_USER_PROFILE = 'SET_USER_PROFILE';
const APDATE_USER_STATUS = 'APDATE_USER_STATUS'
const ADD_NEW_POST = 'ADD_NEW_POST'
const UPDATE_POST_TEXT = 'UPDATE_POST_TEXT'
const DELETE_POST = 'DELETE_POST'
const UPDATE_USER_PHOTO = 'UPDATE_USER_PHOTO'
const LOADING_NEW_PROFILE_PHOTO = 'LOADING_NEW_PROFILE_PHOTO'


let initialState = {
    posts: [] as Array<postsType>,
    newPostText: '',
    profile: null as profileType | null,
    userStatus: '',
    loadingPhoto: false
};

export type initialStateType = typeof initialState


export const profileReducer = (state = initialState, action : any) : initialStateType => {

    switch (action.type) {
        case SET_USER_PROFILE: {
            return { ...state, profile: action.profile }
        }
        case APDATE_USER_STATUS: {
            return {
                ...state,
                userStatus: action.userStatus
            } 
        }
        case UPDATE_USER_PHOTO: {
            return { ...state, profile: { ...state.profile, photos: action.photos } as profileType }
        }
        case UPDATE_POST_TEXT: {
            return {
                ...state,
                newPostText: action.postText
            }
        }
        case ADD_NEW_POST: {
            let newPost = {
                id: 1,
                postText: action.postText,
                userAva: null,
                userName: action.login,
                likesCount: 0,
            };
            return {
                ...state,
                posts: [...state.posts, newPost] as Array<postsType>
            }
        }
        case DELETE_POST: {
            return {
                ...state,
                posts: state.posts.filter(post => post.id != action.postId)
            }
        }
        case LOADING_NEW_PROFILE_PHOTO: {
            return {
                ...state,
                loadingPhoto: !state.loadingPhoto
            }
        }
        default:
            return state;
    }
}


type setUserProfileType = {
    type: typeof SET_USER_PROFILE
    profile: profileType | null
}
const setUserProfile = (profile : profileType | null ) : setUserProfileType => ({ type: SET_USER_PROFILE, profile })

type updateUserStatusACType = {
    type: typeof APDATE_USER_STATUS
    userStatus: string
}
const updateUserStatusAC = (userStatus : string) : updateUserStatusACType => ({ type: APDATE_USER_STATUS, userStatus })

type updateUserPhotoACType = {
    type: typeof UPDATE_USER_PHOTO
    photos: string
}

const updateUserPhotoAC = (photos : string) => ({ type: UPDATE_USER_PHOTO, photos })

type loadingPhotoACType = {
    type: typeof  LOADING_NEW_PROFILE_PHOTO
}
const loadingPhotoAC = () : loadingPhotoACType => ({type: LOADING_NEW_PROFILE_PHOTO})

type addNewPostACType = {
    type: typeof ADD_NEW_POST
    postText: string
    login: string
}
export const addNewPostAC = (postText:string, login:string) : addNewPostACType  => ({ type: ADD_NEW_POST, postText, login })

type deletePostACType = {
    type: typeof  DELETE_POST
    postId: number
}
export const deletePostAC = (postId:number) : deletePostACType => ({ type: DELETE_POST, postId })

export const getUserProfileThunkCreator = (UserId : number) =>
    async (dispatch : any) => {
        const res = await getUserProfile(UserId)
        dispatch(setUserProfile(res.data))
    }
// Делаем запрос на сервер за данными пользователя, и диспатчим данные в стор

export const removeUserProfileThunkCreator = () => {
    return (dispatch : any) => {
        dispatch(setUserProfile(null))
    }
} // Удаляем из стора загруженный профиль

export const getUserStatusThunkCreator = (UserId : number) =>
    async (dispatch : any) => {
        const res = await getUserStatus(UserId)
        dispatch(updateUserStatusAC(res.data))
    }
// Делаем запрос на сервер и диспатчим статус пользователя

export const updateUserStatusThunkCreator = (status : string) =>
    async (dispatch : any) => {
        const res = await sendUserStatus(status)
        if (res.data.resultCode === 0) {
            dispatch(updateUserStatusAC(status))
        }
    }

export const updateUserProfileSettings = (settings : any) =>
    async (dispatch : any) => {
        const res = await sendUserProfileSettings(settings)
    }


export const uploadUserProfilePhotoThunkCreator = (file : any) =>
    async (dispatch : any) => {
        const res = await sendUserPhoto(file)
        if (res.data.resultCode === 0) {
            dispatch(updateUserPhotoAC(res.data.data.photos))
        }
    }

export const addNewPostThunkCreator = (postText : string, login : string) => (dispatch : any) => {
    dispatch(addNewPostAC(postText, login))
}

export const loadingPhotoThunk = () => (dispatch : any) => {
    dispatch(loadingPhotoAC())
}

export default profileReducer;