import {
    getUserProfile,
} from '../API/api'

const SET_USER_PROFILE_PHOTO = 'SET_USER_PROFILE_PHOTO';

let initialState = {
    userProfilePhoto: ''
};


export const headerReducer = (state = initialState, action) => {

    switch (action.type) {
        case SET_USER_PROFILE_PHOTO: {
            return { ...state, userProfilePhoto: action.userProfilePhoto }
        }
        default:
            return state;
    }
}

export const setUserProfilePhoto = (userProfilePhoto) => ({ type: SET_USER_PROFILE_PHOTO, userProfilePhoto })


export const getUserProfilePhotoThunk = (UserId) => 
    async (dispatch) => {
        const res = await getUserProfile(UserId) 
        dispatch(setUserProfilePhoto(res.data.photos.small))
    }

    
