import React from 'react'
import classes from './PageButton.module.css'
import { propsType } from './PaginatorTS'
import cn from 'classnames'

interface PageButtonProps extends propsType {
    button: number
}

const PageButton = (props: PageButtonProps) => {

    const switchPage = () => {
        props.removeUsers()
        props.getUsers(props.pageSize, props.button)
    }

    return (
        <>
            <div>
                <button
                    key={props.button}
                    className={cn({
                        [classes.checkedButton] : props.button === props.pageCount
                    })}
                    onClick={switchPage}>
                    {props.button}
                </button>
            </div>
        </>

    )
}

export default PageButton