import React from 'react'
import { Formik, Form, Field } from 'formik'
import FormikControl from '../Common/FormControls/FormikControl'
import * as Yup from 'yup'


const Settings = (props) => {
    
    const { fullName, aboutMe, lookingForAJob, lookingForAJobDescription } = props.profile
    const { facebook, github, instagram, mainLink, twitter, vk, website, youtube } = props.profile.contacts


    const validationSchema = Yup.object().shape({
        contacts: Yup.object().shape({
            github: Yup.string().url('Invalid URL!'),
            vk: Yup.string().url('Invalid URL!'),
            facebook: Yup.string().url('Invalid URL!'),
            instagram: Yup.string().url('Invalid URL!'),
            twitter: Yup.string().url('Invalid URL!'),
            website: Yup.string().url('Invalid URL!'),
            youtube: Yup.string().url('Invalid URL!'),
            mainLink: Yup.string().url('Invalid URL!')
        })
    })

    const initialValues = {
        fullName: fullName,
        aboutMe: aboutMe,
        lookingForAJob: lookingForAJob,
        lookingForAJobDescription: lookingForAJobDescription,
        contacts: {
            github: github,
            vk: vk,
            facebook: facebook,
            instagram: instagram,
            twitter: twitter,
            website: website,
            youtube: youtube,
            mainLink: mainLink,
        },
    }

    const onSubmit = (values) => {
        console.log('Submitting')
        async function sendProfileSettings() {
            await props.sendProfileSettings(values)
        }
        return sendProfileSettings()
    }

    return (
        
        <>
            <Formik
                initialValues={initialValues}
                onSubmit={onSubmit}
                validationSchema={validationSchema}
                validateOnChange={false}
                >
                {
                    formik => {
                        
                        return <Form>
                            <h1>Profile settings</h1>
                            <FormikControl
                                control='input'
                                label='Full Name'
                                name='fullName'
                            />
                            <FormikControl
                                control='input'
                                label='About me'
                                name='aboutMe'
                            />
                            <FormikControl
                                control='input'
                                label='My skills'
                                name='lookingForAJobDescription'
                            />
                            <div>
                                <p></p>
                                <Field
                                    type='checkbox'
                                    name='lookingForAJob'
                                />
                                <label htmlFor="lookingForAJob">Looking for a job</label>
                            </div>
                            <h3>
                                Contacts
                            </h3>
                            <FormikControl
                                control='input'
                                label='Github'
                                name='contacts.github'
                            />
                            <FormikControl
                                control='input'
                                label='vk'
                                name='contacts.vk'
                            />
                            <FormikControl
                                control='input'
                                label='Facebook'
                                name='contacts.facebook'
                            />
                            <FormikControl
                                control='input'
                                label='Instagram'
                                name='contacts.instagram'
                            />
                            <FormikControl
                                control='input'
                                label='Twitter'
                                name='contacts.twitter'
                            />
                            <FormikControl
                                control='input'
                                label='Website'
                                name='contacts.website'
                            />
                            <FormikControl
                                control='input'
                                label='Youtube'
                                name='contacts.youtube'
                            />
                            <FormikControl
                                control='input'
                                label='Main link'
                                name='contacts.mainLink'
                            />
                            <input type='submit' value='save' disabled={formik.isSubmitting}/>
                        </Form>
                    }
                }
            </Formik>
        </>
    )
}

export default Settings